/*#region cursor*/
.cursor-pointer {
  cursor: pointer;
}

/*#endregion*/

/*#region events*/
.events-none {
  pointer-events: none;
}

.events-unset {
  pointer-events: unset;
}

/* #endregion */

/*#region z-index */
.z-index-1 {
  z-index: 1;
}

.z-index-2 {
  z-index: 2;
}

/* #endregion*/